@import url('https://fonts.googleapis.com/css?family=Lato:400,900');

html, body, #root {
  height: 100%;
  width: 100%;
}

body {
  font-family: 'Lato', sans-serif;
  font-size: 18px;
  font-weight: 400;
  background: #dfe6e9;
}

strong {
  font-weight: 900;
}

.logo-area {
  margin-bottom: 15px;
}

.logo-area img {
  margin: 0 auto;
}

.nav-area ul,
.footer-area ul {
    width: 100%;
    text-align: center;
    margin-bottom: 0;
}

.nav-area ul li {
  margin-bottom: 15px;
}

.nav-area ul li a {
    display: inline-block;
    background: #6c5ce7;
    color: #fff;
    font-weight: 900;
    padding: 3px 15px;
    text-transform: uppercase;
    -webkit-transform: translateY(0);
        -ms-transform: translateY(0);
            transform: translateY(0);
    -webkit-box-shadow: 0 5px 0 rgba(108, 92, 231, 0.75);
            box-shadow: 0 5px 0 rgba(108, 92, 231, 0.75);
}

.nav-area ul li a:hover {
    text-decoration: none;
    -webkit-transform: translateY(5px);
        -ms-transform: translateY(5px);
            transform: translateY(5px);
    -webkit-box-shadow: none;
            box-shadow: none;
}

.card-area {
    position: relative;
    height: 220px;
    overflow-y: scroll;
    background: #fff;
    padding: 20px;
    margin-top: 5px;
    -webkit-box-shadow: 0 5px 30px rgba(156, 160, 171, 0.25), 0 10px 0 -5px rgba(255, 255, 255, 0.75), 0 20px 0 -10px rgba(255, 255, 255, 0.5);
            box-shadow: 0 5px 30px rgba(156, 160, 171, 0.25), 0 10px 0 -5px rgba(255, 255, 255, 0.75), 0 20px 0 -10px rgba(255, 255, 255, 0.5);
}

.card-area:after {
    content: "";
    display: block;
    position: absolute;
    top: 5px;
    left: 0;
    height: 100%;
    width: 100%;
    background: rgba(255, 255, 255, 0.75);
    z-index: -1;
}

.card-area:before {
    content: "";
    display: block;
    position: absolute;
    top: 10px;
    left: 0;
    height: 100%;
    width: 100%;
    background: rgba(255, 255, 255, 0.5);
    z-index: -1;
}

.card-area h4 {
  text-transform: uppercase;
  font-weight: 900;
  margin-bottom: 15px;
}

.skills ul li {
    background: #6c5ce7;
    text-transform: uppercase;
    font-weight: 900;
    color: #fff;
    padding: 5px 15px;
    margin-bottom: 10px;
}

.skills ul li a {
  color: #fff;
}

.footer-area {
    margin-top: 15px;
    text-align: center;
}

.footer-area ul li {
  font-weight: 900;
  text-transform: uppercase;
  font-size: 15px;
  color: rgba(0, 0, 0, 0.5);
}

.footer-area ul li:after {
    content: "";
    display: inline-block;
    position: relative;
    top: -3px;
    background: rgba(0, 0, 0, 0.25);
    height: 5px;
    width: 5px;
    margin-left: 5px;
    border-radius: 50%;
}

.footer-area ul li:last-child:after {
  display: none;
}



